*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

body {
  background-color: pink;
  font-family: Arial, Helvetica, sans-serif;
}

#app {
  justify-content: center;
  align-items: center;
  height: 100dvh;
  display: flex;
}

.container {
  grid-template-columns: 1fr, 1fr;
  grid-template-rows: 1fr, 1fr;
  display: grid;
}

.player-section {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

#section1 {
  grid-area: 1 / 1;
}

#section2 {
  grid-area: 1 / 2;
}

.player-board {
  border: 3px solid #000;
  width: 350px;
  height: 350px;
  margin-inline: 4rem;
  display: grid;
}

.turn {
  border: 5px solid gold;
}

.cell {
  background-color: pink;
  outline: 1px solid #888;
}

.cell--hidden {
  background-color: #f4d9d9;
}

.cell--hidden:hover {
  background-color: #d4b2b2;
}

.cell--hit {
  background-color: #dfa7a7;
}

.cell--ship-part {
  background-color: #444;
}

.cell--hover {
  background-color: #666;
}

.cell--hit-ship {
  background-color: #d00;
}

.player-name {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}

.player-ships {
  justify-content: center;
  align-items: flex-end;
  gap: 1rem;
  display: flex;
}

.small-ship {
  border: 1px solid #333;
  flex-direction: column;
  display: flex;
}

.small-cell {
  background-color: #666;
  border-top: 1px solid #333;
  width: .75rem;
  height: .75rem;
}

.small-cell:first-child {
  border-top: 0;
}

.small-cell--dead {
  background-color: #d00;
}

.text-field {
  text-align: center;
  grid-column: 1 / span 2;
  padding-top: 3rem;
  font-size: 2rem;
}

